import React, { Component } from 'react'
import {Cell, Grid, ProgressBar} from 'react-mdl'

export class Skills extends Component {
    render() {
        return (
            <Grid>
            <Cell col={2}>
              <p style={{marginTop:"-10px"}}>{this.props.skillName}</p>
            </Cell>
            <Cell col={10}>
            <ProgressBar progress={this.props.skillValue} />
            </Cell>
          </Grid>
        )
    }
}

export default Skills
